// import layoutMain from '@/layout/main'
import layoutHeader from '@/layout/header'
import home from '@/views/home'

const meta = {
  requiresAuth: true
}

/**
 * 在主框架之内显示
 */
const frameIn = [{
  path: '/',
  redirect: '/home',
  meta,
  component: layoutHeader,
  children: [{
    path: '/home',
    name: 'home',
    meta,
    component: home,
  }, {
    path: '/video',
    name: 'video',
    meta,
    component: () => import('@/views/vip-video'),
    // children: (pre => [])('video-')
  },  {
    path: '/joke',
    name: 'joke',
    meta,
    component: () => import('@/views/joke'),
    // children: (pre => [])('joke-')
  }, {
    path: '/author',
    name: 'author',
    meta,
    component: () => import('@/views/author'),
  }, {
    path: '/setting',
    name: 'setting',
    meta,
    component: () => import('@/views/setting'),
  }]
}]
/**
 * 在主框架之外显示
 */
const frameOut = [
  // 页面重定向使用 必须保留
  {
    path: '/redirect/:path*',
    component: {
      beforeCreate () {
        this.$router.replace(JSON.parse(this.$route.params.path))
      },
      render: h => h()
    }
  },
  // 页面刷新 必须保留
  {
    path: '/refresh',
    name: 'refresh',
    component: {
      beforeRouteEnter (to, from, next) {
        next(vm => {
          vm.$router.replace(from.fullPath)
        })
      },
      render: h => h()
    }
  },
  // index
  {
    path: '/index',
    name: 'index',
    component: () =>
      import('@/views/index')
  },
  //about
  {
    path: '/about',
    name: 'about',
    component: () =>
      import('@/views/about')
  }]

// 重新组织后导出
export default [
  ...frameIn,
  ...frameOut
]
