import request from '@/plugin/axios'

/**
 * http://gank.io/api
 * 干货集中营API
 */
// 最新干货
export function getLatestGank () {
  return request({
    url: `http://gank.io/api/today`,
    method: 'get'
  })
}

// 获取闲读的主分类
export function getXianduCategories () {
  return request({
    url: `http://gank.io/api/xiandu/categories`,
    method: 'get'
  })
}

// 获取闲读的子分类
export function getXianduCategory ({ param }) {
  return request({
    url: `http://gank.io/api/xiandu/category/${param.category}`,
    method: 'get'
  })
  // 注：
  // category 后面可接受参数为主分类返回的en_name,例如【apps， wow， android，iOS】
}

// 获取闲读数据
export function getXianduData ({ param }) {
  return request({
    url: `http://gank.io/api/xiandu/data/id/${param.id}/count/${param.count}/page/${param.page || 1}`,
    method: 'get'
  })
  // 获取闲读数据
  // 例如： http://gank.io/api/xiandu/data/id/appinn/count/10/page/1
  // 注：
  // id 后面可接受参数为子分类返回的id
  // page 第几页，从1开始
  // count 每页的个数
}

// 查询干货
export function getSearchGank ({ param }) {
  return request({
    url: `http://gank.io/api/search/query/listview/category/${param.category}/count/${param.count}/page/${param.page}`,
    method: 'get'
  })
  // 注：
  // category 后面可接受参数 all | Android | iOS | 休息视频 | 福利 | 拓展资源 | 前端 | 瞎推荐 | App
  // count 最大 50
}
