import axios from 'axios'
import { Message } from 'element-ui'

// 记录和显示错误
function errorLog (err) {
  // 打印到控制台
  console.log(err)
  // 显示提示
  Message({
    message: err.message,
    type: 'error',
    duration: 5 * 1000
  })
}

// 创建一个 axios 实例
const service = axios.create({
  // baseURL: process.env.VUE_APP_API, // 接口前缀
  timeout: 5000 // 请求超时时间
})

// 根据不同服务器 选择相应接口前缀
// if (process.env.VUE_APP_BUILD_MODE === 'DEV') {
//   // 开发服务器
//   service.defaults.baseURL = 'https://dev.hjhx.cn/api'
// } else if (process.env.VUE_APP_BUILD_MODE === 'TEST') {
//   // 测试服务器
//   service.defaults.baseURL = 'https://dev.hjhx.cn/api'
// } else if (process.env.VUE_APP_BUILD_MODE === 'PRO') {
//   // 生产服务器
//   service.defaults.baseURL = 'https://api.hjhx.cn/'
// } else {
//   service.defaults.baseURL = 'https://dev.hjhx.cn/api'
// }
//
// console.log('service.defaults.baseURL', service.defaults.baseURL)

// 请求拦截器
service.interceptors.request.use(
  config => {
    // 在请求发送之前做一些处理
    // config.headers = {
    //   'Content-Type': 'application/json',
    //   "Access-Control-Allow-Origin": "*"
    // }
    return config
  },
  error => {
    // 发送失败
    console.log(error)
    Promise.reject(error)
  }
)

// 响应拦截器
service.interceptors.response.use(
  response => {
    // dataAxios 是 axios 返回数据中的 data
    const dataAxios = response.data
    // 根据前后端约定 判断返回值的对应操作 （可操作自动退出登录等）
    return dataAxios
  },
  error => {
    if (error && error.response) {
      switch (error.response.status) {
        case 400:
          error.message = '请求错误, 参数不符合 API 的要求'
          break
        case 401:
          error.message = '用户认证失败，或缺少认证信息'
          break
        case 403:
          error.message = '当前用户对资源没有操作权限'
          break
        case 404:
          error.message = `请求地址出错: ${error.response.config.url}`
          break
        case 408:
          error.message = '请求超时'
          break
        case 500:
          error.message = '服务器异常'
          break
        case 501:
          error.message = '服务未实现'
          break
        case 502:
          error.message = '网关错误'
          break
        case 503:
          error.message = '服务不可用'
          break
        case 504:
          error.message = '网关超时'
          break
        case 505:
          error.message = 'HTTP版本不受支持'
          break
        default:
          break
      }
    } else {
      if (error.toString().indexOf('timeout') !== -1) {
        error.message = '请求超时'
      } else {
        error.message = '网络错误'
      }
    }
    errorLog(error)
    return Promise.reject(error)
  }
)

export default service
