import $$ from 'jquery'

/**
 * https://blog.csdn.net/hlx20080808/article/details/83274513
 * 网络分享 豆瓣电影 API
 */
export function getLatestMovie () {
  return $$.ajax({
    type: 'GET',
    // async: true,    //是否异步
    url: 'https://api.douban.com/v2/movie/in_theaters?city=青岛&start=0&count=10',
    dataType: 'json',
    // success: function (data) {
    //   console.log('success', data)
    // },
    // error: function (err) {
    //   console.log('Error', err)
    // }
  })
}
